import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { CourseComponent } from './components/course/course/course.component';
import { DashboardComponent } from './components/dashboard/dashboard/dashboard.component';
import { HomeComponent } from './components/home/home/home.component';
import { LoginComponent } from './components/login/login/login.component';
import { RegisterComponent } from './components/register/register/register.component';
import { UpdatecourseComponent } from './components/updatecourse/updatecourse/updatecourse.component';
import { UsercoursesComponent } from './components/usercourses/usercourses/usercourses.component';
import { ViewcourseComponent } from './components/viewcourse/viewcourse/viewcourse.component';

import {AuthGuard} from "./guards/auth.guard";

const routes: Routes = [
  {
    path: '',
    component: HomeComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'register',
    component: RegisterComponent
  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'course',
    component: CourseComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'course/:id',
    component: ViewcourseComponent
  },
  {
    path: 'course/:id/edit',
    component: UpdatecourseComponent
  },
  {
    path: 'user/:id',
    component: UsercoursesComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { scrollPositionRestoration: 'top' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
