import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class CourseService {

  constructor(private http: HttpClient, private auth: AuthService) { }

  getCourses(query?: Object) {
    if (query) return this.http.get(`/course?page=${query['page']}&limit=${query['limit']}`);
    return this.http.get(`/course`);
  }

  getUserCourses(id, query?: Object) {
    if (query) return this.http.get(`/course/user/${id}?page=${query['page']}&limit=${query['limit']}`);
    return this.http.get(`/course/user/${id}`);
  }

  uploadCover(file) {

    const formData = new FormData();
    formData.append('file', file);

    let headers = new HttpHeaders();
    headers.set('Content-Type', 'multipart/form-data');

    return this.http.post('/upload', formData, {
      reportProgress: true,
      observe: 'events',
      headers: headers
    });
  }

  addCourse(course: any) {
    let headers = new HttpHeaders();
    headers.set('Content-Type', 'application/json');
    return this.http.post('/course', course, { headers: headers });
  }

  getCourse(id: any) {
    return this.http.get(`/course/${id}`);
  }

  updateCourse(id: any, update: any) {
    return this.http.put(`/course/${id}`, update);
  }

  deleteCourse(id: any) {
    return this.http.delete(`/course/${id}`);
  }
}
