import { HttpEventType } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormControlName, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AngularEditorConfig } from '@kolkov/angular-editor';
import { AuthService } from 'src/app/services/auth.service';
import { CourseService } from '../../../services/course.service';

@Component({
  selector: 'app-course',
  templateUrl: './course.component.html',
  styleUrls: ['./course.component.sass']
})
export class CourseComponent implements OnInit {


  courseForm: FormGroup;
  error: any;
  htmlContent: FormControlName;
  progress;
  file;
  cover_url;

  editorConfig: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: 'auto',
    minHeight: '300px',
    maxHeight: 'auto',
    width: 'auto',
    minWidth: '0',
    translate: 'yes',
    enableToolbar: true,
    showToolbar: true,
    placeholder: 'Enter text here...',
    defaultParagraphSeparator: '',
    defaultFontName: '',
    defaultFontSize: '',
    fonts: [
      { class: 'arial', name: 'Arial' },
      { class: 'times-new-roman', name: 'Times New Roman' },
      { class: 'calibri', name: 'Calibri' },
      { class: 'comic-sans-ms', name: 'Comic Sans MS' }
    ],
    customClasses: [
      {
        name: 'quote',
        class: 'quote',
      },
      {
        name: 'redText',
        class: 'redText'
      },
      {
        name: 'titleText',
        class: 'titleText',
        tag: 'h1',
      },
    ],
    uploadUrl: 'http://localhost:3000/upload',
    uploadWithCredentials: false,
    sanitize: true,
    toolbarPosition: 'top',
    toolbarHiddenButtons: [
      ['bold', 'italic'],
      ['fontSize', 'insertImage', 'insertVideo']
    ]
  };

  constructor(private router: Router, private auth: AuthService, private cs: CourseService) { }

  ngOnInit(): void {
    document.getElementsByClassName('angular-editor-textarea')[0]['style']['padding-left'] = '32px';
    this.courseForm = new FormGroup({
      'title': new FormControl('', [
        Validators.required
      ]),
      'description': new FormControl('', [
        Validators.required
      ]),
      'module': new FormControl('', [
        Validators.required
      ]),
      'cover': new FormControl('', [
        Validators.required
      ]),
    });
  }

  get title() { return this.courseForm.get('title'); }
  get description() { return this.courseForm.get('description'); }
  get module() { return this.courseForm.get('module'); }
  get cover() { return this.courseForm.get('cover'); }

  onSubmit() {
    if (this.file) {
      this.uploadImage();
    } else {
      this.addCourse();
    }
  }

  uploadImage() {
    console.log("Uploading image now");

    this.cs.uploadCover(this.file)
      .subscribe(event => {
        if (event.type === HttpEventType.UploadProgress) {
          this.progress = Math.round((100 * event.loaded) / event.total);
          console.log(this.progress);
        } else if (event.type === HttpEventType.Response) {
          console.log(event.body);
          this.cover_url = event.body['filename'] || null;
          console.log("Cover url: " + this.cover_url);
          this.addCourse();
        }
      });
  }

  addCourse() {
    console.log("Adding course now");

    let course = {
      title: this.title.value,
      description: this.description.value,
      module: this.module.value,
      content: this.htmlContent,
      cover: this.cover_url,
      user: this.auth.getUser()
    }
    this.cs.addCourse(course).subscribe(data => {
      if (data['success']) {
        this.router.navigate(['/course/' + data['course']['_id']]);
      }
    });
  }

  processFile(imageInput: any) {
    this.file = imageInput.files[0];
    console.log(this.file);
  }
}
