import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { CourseService } from 'src/app/services/course.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.sass']
})
export class DashboardComponent implements OnInit {

  userID: any;
  courses: Object[];
  currentPage;
  totalPages;

  constructor(private route: ActivatedRoute, private cs: CourseService, private auth: AuthService) { }

  ngOnInit(): void {
    this.userID = this.auth.getUser()['_id'];
    this.loadPage(this.route.snapshot.queryParams['page'] || 1);
  }

  loadPage(page: Number) {
    this.cs.getUserCourses(this.userID, {page: page, limit: 4}).subscribe(data => {
      if (data['success']) {
        this.courses = data['courses'];
        this.currentPage = data['currentPage'];
        this.totalPages = data['totalPages'];
      }
    });
  }

  delete(index) {
    this.cs.deleteCourse(this.courses[index]['_id']).subscribe(data => {
      if (data['success']) {
        this.courses.splice(index, 1);
      } else {
        console.error("Couldn't delete!");
      }
    }, err => console.error(err));
  }

  arrayOf(n: number, startFrom: number): number[] {
    return [...Array(n).keys()].map(i => i + startFrom);
  }
}
